import * as React from "react";
import { useSelector } from 'react-redux';

//import { selectTempData } from "../features/temp/tempSlice";
import { selectCaseArray, selectCaseLayoutStatusById } from "../features/case/caseSlice"

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";


export default function ShowFinancialResults() {
    const status = useSelector(selectCaseLayoutStatusById("showfinancialresults"));
    const records = useSelector(selectCaseArray);
  //  const periods = useSelector(selectCasePeriods);

    let periods = [...new Set(records.map(item => item.period))];

    function checkTypePeriod(indicator, periodIndex) {
        let sum = 0;
        //    console.log(indicator, periodIndex);
        let processed = !!records && Array.isArray(records) && records.length > 0 && records.map((item) => {
            //   console.log(item.period, periods[periodIndex]);
            if (
                !!item?.type &&
                item.type.includes(indicator) &&
                item.period === periods[periodIndex - 1]
            ) {

                sum = sum + parseFloat(item.sum);
                //   console.log(sum);
            }
            return null;
        });
        return sum;
    }

    if (!status) { return null }

    return <Container className="mt-5 animated fadeIn">
        <Row className="bg-secondary text-white">
            {["Финансовые результаты", ...periods].map((item, index) => (
                <Col key={index} xs={index > 0 ? 1 : 12-periods.length}>
                    {item}
                </Col>
            ))}
        </Row>

        {["Выручка", "Себестоимость продукции, работ, услуг", "Коммерческие расходы", "Управленческие расходы",
            "Проценты к уплате", "Прочие расходы", "Налог на прибыль", "Дивиденды к начислению"]
            .map((row, indexRow) => <Row key={indexRow}>
                {[row, ...periods].map((item, index) =>
                    <Col key={index} xs={index > 0 ? 1 : 12-periods.length}>
                        {index === 0 ? row : checkTypePeriod(row, index)}
                    </Col>
                )}
            </Row>
            )}

    </Container>
}