import * as React from "react";
import { useSelector } from 'react-redux';

//import { selectTempData } from "../features/temp/tempSlice";
import { selectCaseArray, selectCaseLayoutStatusById, selectCasePeriods } from "../features/case/caseSlice"

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import processRecords from "../../../utilities/processRecords"


const balanceIndicators = [

    { id: 1, title: "", type: 'asset' },
    { id: 2, title: "Деньги", type: 'asset' },
    { id: 3, title: "Дебиторская задолженность", type: 'asset' },
    { id: 4, title: "Готовая продукция", type: 'asset' },
    { id: 5, title: "Незавершенное производство", type: 'asset' },
    { id: 6, title: "Материалы", type: 'asset' },

    { id: 7, title: "Основные средства", type: 'asset' },


    { id: 8, title: "", type: "liability" },

    { id: 9, title: "Кредиторская задолженность", type: 'liability' },
    { id: 10, title: "Краткосрочный банковский кредит", type: 'liability' },
    { id: 11, title: "Долгосрочный банковский кредит", type: 'liability' },
    { id: 12, title: "Нераспределенная прибыль", type: 'equity' },

    { id: 13, title: "", type: 'equity' },

    { id: 14, title: "Уставный капитал", type: 'equity' }
]


export default function ShowBalance() {
    // const records = useSelector(selectTempData).dataArray;
    const status = useSelector(selectCaseLayoutStatusById("showbalance"));
    const records = useSelector(selectCaseArray); //.slice(0, 2)
   // const periods = useSelector(selectCasePeriods);

    let periods = [...new Set(records.map(item => item.period))];

    //     let series = [];
    // balanceIndicators.forEach(element => {
    //   console.log(element);
    //     if (element.title.length > 2) {
    //         series.push({
    //             name: element.title,
    //             data: processBalanceIndicatorForStackedBarsChart(element.title, records, periods, balanceIndicators)
    //         })
    //     }
    // });

    //    console.log(series);

    if (!status) { return null }

    return <Container className="mt-5 animated fadeIn">
        <Row className="bg-secondary text-white">
            {["Активы", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {period}
                </Col>
            ))}
        </Row>
        <Row>
            {["Основные средства", ...periods].map((period, index) => (
                <Col className="font-weight-bold" key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0
                        ? "Основные средства"
                        : processRecords( //processRecords("Основные средства", period)}
                            {
                                indicator: "Основные средства",
                                balanceIndicators: balanceIndicators,
                                records: records,
                                periods,
                                period
                            }
                        )
                    }
                </Col>
            ))}
        </Row>
        <Row>
            {["Материалы", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0 ? "Материалы"  ////    processRecords("Материалы", period)}
                        : processRecords(
                            {
                                indicator: "Материалы",
                                balanceIndicators: balanceIndicators,
                                records: records,
                                periods,
                                period
                            }
                        )}


                </Col>
            ))}
        </Row>
        <Row>
            {["Незавершенное производство", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0
                        ? "Незавершенное производство"
                        : processRecords({
                            indicator: "Незавершенное производство",
                            balanceIndicators: balanceIndicators,
                            records: records,
                            periods,
                            period
                        })}
                </Col>
            ))}
        </Row>
        <Row>
            {["Готовая продукция", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0
                        ? "Готовая продукция"
                        : processRecords({
                            indicator: "Готовая продукция",
                            balanceIndicators: balanceIndicators,
                            records: records,
                            periods,
                            period
                        })}
                </Col>
            ))}
        </Row>

        <Row>
            {["Дебиторская задолженность", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0
                        ? "Дебиторская задолженность" //    : processRecords("Дебиторская задолженность", period)}
                        : processRecords({
                            indicator: "Дебиторская задолженность",
                            balanceIndicators,
                            records: records,
                            periods,
                            period
                        })}

                </Col>
            ))}
        </Row>
        <Row>
            {["Деньги", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0 ? "Деньги" :
                     processRecords({indicator: "Деньги",
                            balanceIndicators: balanceIndicators,
                            records: records,
                            periods,
                            period})}
                </Col>
            ))}
        </Row>
        <Row className="bg-secondary text-white">
            {["Пассивы", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {period}
                </Col>
            ))}
        </Row>
        <Row>
            {["Уставный капитал", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0
                        ? "Уставный капитал" //processRecords("Уставный капитал", period)}
                        : processRecords(
                            {
                                indicator: "Уставный капитал",
                                balanceIndicators,
                                records: records,
                                periods,
                                period
                            }
                        )}
                </Col>
            ))}
        </Row>
        <Row>
            {["Нераспределенная прибыль", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0
                        ? "Нераспределенная прибыль"
                        : processRecords({indicator: "Нераспределенная прибыль",
                        balanceIndicators: balanceIndicators,
                        records: records,
                        periods,
                        period})}
                </Col>
            ))}
        </Row>
        <Row className="bg-light text-dark">
            {["Долгосрочный банковский кредит", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0
                        ? "Долгосрочный банковский кредит"
                        : processRecords({indicator: "Долгосрочный банковский кредит",
                        balanceIndicators: balanceIndicators,
                        records: records,
                        periods,
                        period})}
                </Col>
            ))}
        </Row>
        <Row className="bg-light text-dark">
            {["Краткосрочный банковский кредит", ...periods].map(
                (period, index) => (
                    <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                        {index === 0
                            ? "Краткосрочный банковский кредит"
                            : processRecords({indicator:  "Краткосрочный банковский кредит",
                            balanceIndicators: balanceIndicators,
                            records: records,
                            periods,
                            period})}
                    </Col>
                )
            )}
        </Row>
        <Row className="bg-light text-dark">
            {["Кредиторская задолженность", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0
                        ? "Кредиторская задолженность"
                        : processRecords({indicator:  "Кредиторская задолженность",
                        balanceIndicators: balanceIndicators,
                        records: records,
                        periods,
                        period})}
                </Col>
            ))}
        </Row>
    </Container>

}