function AccountingNavBar() {
  const dispatch = useDispatch();
  const caseLayout = useSelector(selectCaseLayout);

  if (caseLayout.length > 0) {
    //    console.log(caseLayout);
    function doToggle(id, status) {
      let updatedCaseLayout = caseLayout.map(obj => {
        if (obj.id === id) {
          return { id: obj.id, label: obj.label, status: status }
        }
        return obj
      });
      console.log(updatedCaseLayout);
      dispatch(seedArray({ arrayName: "caseLayout", arrayItems: updatedCaseLayout }));
    }

    return <div className="mt-2 container p-2 border rounded border-primary">

      <div className="row g-3 align-items-center">

        {caseLayout.map(item => <div className="col-auto" key={item.id}>
          <div className="form-check">
            <input className="form-check-input" type="checkbox" checked={item.status} id={item.id} onChange={() => doToggle(item.id, !item.status)} />
            <label className="form-check-label" htmlFor={item.id}>
              {item.label}
            </label>
          </div>
        </div>)}
      </div>
    </div>
  }

  return null
}

function SelectCase() {
  const dispatch = useDispatch();
  const status = useSelector(selectCaseLayoutStatusById("showselectcase"));
  const casesArray = useSelector(selectCasesArray);

  console.log(casesArray)

  if (status) {

    function doSelect(item) {
      dispatch(setStoreObject({ key: "selectedCase", value: item }));
      dispatch(setStoreObject({ key: "caseComment", value: item.comment }));
      dispatch(seedArray({ arrayName: "caseArray", arrayItems: !!item?.content && Array.isArray(item.content) ? item.content : [] }));
      dispatch(seedArray({ arrayName: "caseArrayTrash", arrayItems: !!item?.trash && Array.isArray(item.trash) ? item.trash : [] }));
      dispatch(updateItemInArrayById({ arrayName: "caseLayout", item: { id: "showbalance", status: true, label: "Баланс" } }));
    }

    return <div>
      <Container className="mt-2 border rounded border-success">
        {Array.isArray(casesArray) && casesArray.map(item =>
          <Row key={item.id} className="p-1">
            <Col><small>{item.title}</small></Col>
            <Col>{item.date}</Col>
            <Col>
              <Button
                variant="outline-secondary"
                size="sm"
                onClick={() => doSelect(item)}
              >
                Открыть
              </Button>
            </Col>
          </Row>
        )}
      </Container>
    </div>
  } else {
    return null
  }
}



function CreateCase() {
  const { register, handleSubmit } = useForm();
  const status = useSelector(selectCaseLayoutStatusById("showcreatecase"));
  const applicationState = useSelector(selectApplication);

  if (status && !!applicationState?.email) {

    function onSubmit(data) {
      console.log(data);

      let userEmail = applicationState.email.replace(/[^a-zA-Z0-9]/g, "_");

      let idPost = pushKeyFirebaseNode("/usersCraft/" + userEmail + "/posts/");
      let postObject = {
        id: idPost,
        title: data.title,
        theme: "Планирование и бюджетирование",
        answer: "Операции и прогнозная отчетность",
        comment: data.comment,
        type: "accountingwithprofitscash",
        content: [],
        quizString: "Операции и прогнозная отчетность",
        deleted: false,
        user: applicationState?.user,
        email: applicationState?.email,
        avatarUrl: !!applicationState?.avatarUrl ? applicationState.avatarUrl : null,
        date: new Intl.DateTimeFormat("ru", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric",
          hour: "numeric",
          minute: "numeric",
        }).format(new Date()), //Date().toJSON()
      }

      console.log(postObject);
      let updates = {};
      updates["/usersCraft/" + userEmail + "/posts/" + idPost] = postObject;
      updates["/openmedia/" + idPost] = postObject;
      updates[
        "/usersTemplates/projects/" + userEmail + "/" + idPost
      ] = postObject;
      updateFirebaseNode(updates, "reload");

      //           updateAccountingWithProfitsCashProject(dbRef, selectedCase, userEmail)
      //               .then(() => window.location.reload());
    }


    return <div>
      <Container>
        <Form onSubmit={handleSubmit(onSubmit)} className="p-3">
          <Form.Group className="mb-3" controlId="formTitle">
            <Form.Label>Название проекта</Form.Label>
            <Form.Control type="text" placeholder="Название" {...register("title")} required />
            <Form.Text className="text-muted">
              Желательно раскрыть суть проекта (компанию) и указать дату или месяц текущего варианта
            </Form.Text>
          </Form.Group>

          <Form.Group className="mb-3" controlId="formComment">
            <Form.Label>Комментарий</Form.Label>
            <Form.Control type="text" placeholder="Комментарий" {...register("comment")} />
            <Form.Text className="text-muted">
              Вариант проекта (базовый, оптимистический, пессимистический, разностный)
            </Form.Text>
          </Form.Group>

          <Button variant="outline-secondary" size="sm" type="submit">
            Сохранить
          </Button>
        </Form>
      </Container>
    </div>
  } else {
    return null
  }
}





function AccountingMachine() {
  const status = useSelector(selectCaseLayoutStatusById("showaccountingmachine"));
  const applicationState = useSelector(selectApplication);
  const dispatch = useDispatch();
  const { register, handleSubmit, reset, watch } = useForm(); //, watch, errors
  const periods = useSelector(selectCasePeriods);
  const records = useSelector(selectCaseArray);
  const currentCase = useSelector(selectedCase);

  if (!status) { return null }

  console.log(currentCase);
  const user = applicationState?.user;
  const email = applicationState?.email;

  function onSave(updatedRecs) {
    if (email.length > 6) {
      let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
      let idPost = !!currentCase?.id
        ? currentCase.id
        : pushKeyFirebaseNode("/usersCraft/" + userEmail + "/posts/")

      let postObject = {
        id: idPost, // "accountingwithprofitscash",
        title: !!currentCase?.title ? currentCase.title : "Планирование и бюджетирование по трем формам",
        theme: !!currentCase?.theme ? currentCase.theme : "Планирование и бюджетирование",
        answer: "Операции и прогнозная отчетность",
        comment: !!currentCase?.comment ? currentCase.comment : "Лекционный пример",
        type: "accountingwithprofitscash",
        content: updatedRecs,
        quizString: !!currentCase?.quizString ? currentCase.quizString : null,
        deleted: false,
        email: email,
        user: user,
        avatarUrl: !!applicationState?.avatarUrl ? applicationState.avatarUrl : null,
        date: new Intl.DateTimeFormat("ru", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric",
          hour: "numeric",
          minute: "numeric",
        }).format(new Date()), //Date().toJSON()
      };

      console.log(postObject);
      //     //    dispatch(createPost(postObject));
      let updates = {};
      updates["/usersCraft/" + userEmail + "/posts/" + idPost] = postObject;
      updates["/openmedia/" + idPost] = postObject;
      updates[
        "/usersTemplates/projects/" + userEmail + "/" + idPost
      ] = postObject;
      //  console.log(pushKeyFirebaseNode("/usersCraft/" + userEmail + "/posts/"));
      updateFirebaseNode(updates, null);
    }
  }

  const onSubmit = (data) => {
    console.log(data);
    setTimeout(onSave([...records, data]), 1000);
    dispatch(pushItemToArray({ arrayName: "caseArray", item: data }));
    reset({ period: null, d: "...", k: "...", sum: null });
  };

  const capitalIncrease =
    watch("k") === "Нераспределенная прибыль" ? true : false;
  const capitalDecrease =
    watch("d") === "Нераспределенная прибыль" ? true : false;
  const cashIncrease = watch("d") === "Деньги" ? true : false;
  const cashDecrease = watch("k") === "Деньги" ? true : false;
  const costsCalculation =
    watch("d") === "Незавершенное производство" ? true : false;

  return (
    <div className="mt-2 p-1">
      <div className="p-3 mb-2 bg-secondary text-white">Новая операция</div>
      <form onSubmit={handleSubmit(onSubmit)}>
        <div>
          <Form.Group as={Col} controlId="formStatePeriod">
            <Form.Label>Период</Form.Label>
            <Form.Control as="select"  {...register("period")} required>
              {["...", ...periods].map((item) => (
                <option key={item}>{item}</option>
              ))}
            </Form.Control>
          </Form.Group>
        </div>
        <div>
          <Form.Group as={Col} controlId="formStateD">
            <Form.Label>Актив+ или Пассив- (Дт)</Form.Label>
            <Form.Control as="select" {...register("d")} required>
              <option>...</option>
              <option>Основные средства</option>
              <option>Материалы</option>
              <option>Незавершенное производство</option>
              <option>Готовая продукция</option>
              <option>Дебиторская задолженность</option>
              <option>Деньги</option>
              <option>Уставный капитал</option>
              <option>Нераспределенная прибыль</option>
              <option>Долгосрочный банковский кредит</option>
              <option>Краткосрочный банковский кредит</option>
              <option>Кредиторская задолженность</option>
            </Form.Control>
          </Form.Group>
          <Form.Group as={Col} controlId="formStateK">
            <Form.Label>Актив- или Пассив+ (Кт)</Form.Label>
            <Form.Control as="select" {...register("k")} required>
              <option>...</option>
              <option>Основные средства</option>
              <option>Материалы</option>
              <option>Незавершенное производство</option>
              <option>Готовая продукция</option>
              <option>Дебиторская задолженность</option>
              <option>Деньги</option>
              <option>Уставный капитал</option>
              <option>Нераспределенная прибыль</option>
              <option>Долгосрочный банковский кредит</option>
              <option>Краткосрочный банковский кредит</option>
              <option>Кредиторская задолженность</option>
            </Form.Control>
          </Form.Group>
          <Form.Group as={Col} controlId="formStateSum">
            <Form.Label>Сумма</Form.Label>
            <Form.Control as="input" {...register("sum")} required />
          </Form.Group>
        </div>

        {capitalIncrease ? (
          <div>
            <Form.Group as={Col} controlId="formCapitalIncrease">
              <Form.Label>Увеличение капитала (прибыль, доход)</Form.Label>
              {/* <Form.Control as="select" name="type" ref={register} required> */}
              <Form.Control as="select"  {...register("type")} required>
                <option>...</option>
                <option>Выручка</option>
                <option>Прочие доходы</option>
                <option>Дивиденды к получению</option>
                <option>Проценты к получению</option>
              </Form.Control>
            </Form.Group>
          </div>
        ) : null}

        {capitalDecrease ? (
          <div>
            <Form.Group as={Col} controlId="formCapitalDecrease">
              <Form.Label>Уменьшение капитала (убыток, расходы)</Form.Label>
              <Form.Control as="select" {...register("type")} required>
                <option>...</option>
                <option>Себестоимость продукции, работ, услуг</option>
                <option>Коммерческие расходы</option>
                <option>Управленческие расходы</option>
                <option>Проценты к уплате</option>
                <option>Прочие расходы</option>
                <option>Налог на прибыль</option>
                <option>Дивиденды к начислению</option>
              </Form.Control>
            </Form.Group>
          </div>
        ) : null}

        {cashIncrease ? (
          <div>
            <Form.Group as={Col} controlId="formCashIncrease">
              <Form.Label>Поступление денежных средств</Form.Label>
              <Form.Control as="select" name="type" {...register("type")} required>
                <option>...</option>
                <option>Поступления по текущей деятельности</option>
                <option>Поступления по инвестиционной деятельности</option>
                <option>Поступления по финансовой деятельности</option>
              </Form.Control>
            </Form.Group>
          </div>
        ) : null}

        {cashDecrease ? (
          <div>
            <Form.Group as={Col} controlId="formCashDecrease">
              <Form.Label>Платежи (выбытие) денежных средств</Form.Label>
              <Form.Control as="select"  {...register("type")} required>
                <option>...</option>
                <option>Платежи по текущей деятельности</option>
                <option>Платежи по инвестиционной деятельности</option>
                <option>Платежи по финансовой деятельности</option>
              </Form.Control>
            </Form.Group>
          </div>
        ) : null}

        {costsCalculation ? (
          <div>
            <Form.Group as={Col} controlId="formCostsCalculation">
              <Form.Label>Статья калькуляции</Form.Label>
              <Form.Control as="select"  {...register("type")} required>
                <option>...</option>
                <option>Материальные затраты (Прямые затраты)</option>
                <option>Оплата труда и соцстрахование (Прямые затраты)</option>
                <option>Амортизация (Постоянные затраты)</option>
                <option>Услуги и работы (Прямые затраты)</option>
                <option>Услуги и работы (Косвенные затраты)</option>
                <option>Реклама (Косвенные затраты)</option>
                <option>Материальные затраты (Косвенные затраты)</option>
                <option>
                  Оплата труда и соцстрахование (Косвенные затраты)
                </option>
                <option>
                  Управленческие расходы (другие Косвенные затраты)
                </option>
                <option>Коммерческие расходы (другие Косвенные затраты)</option>
              </Form.Control>
            </Form.Group>
          </div>
        ) : null}

        <Button variant="outline-secondary" type="submit" className="m-2">
          Провести операцию
        </Button>
      </form>
    </div>
  );
}

