function ShowComment() {
  const dispatch = useDispatch();
  const applicationState = useSelector(selectApplication);
  const currentCase = useSelector(selectedCase);
  const status = useSelector(selectCaseLayoutStatusById("showcomment"));
  const commentDefaultValue = useSelector(selectCaseComment);
  const [value, setValue] = React.useState(commentDefaultValue);

  const debounced = useDebouncedCallback(
    // function
    (value) => {
      setValue(value);
      dispatch(setStoreObject({ key: "caseComment", value: value }));
      if (!!applicationState?.email && !!currentCase?.id) {
        let userEmail = applicationState.email.replace(/[^a-zA-Z0-9]/g, "_");
        let updates = {};
        updates["/usersCraft/" + userEmail + "/posts/" + currentCase.id + "/comment/"] = value;
        updates["/openmedia/" + currentCase.id + "/comment/"] = value;;
        updates[
          "/usersTemplates/projects/" + userEmail + "/" + currentCase.id + "/comment/"] = value;
        //  console.log(pushKeyFirebaseNode("/usersCraft/" + userEmail + "/posts/"));
        updateFirebaseNode(updates, null);
      }

    },
    // delay in ms
    3000
  );

  if (status && typeof window !== "undefined") {

    // you should use `e => debounced(e.target.value)` as react works with synthetic events
    return (
      <Container className="mt-3">
        {/* <textarea defaultValue={commentDefaultValue} onChange={(e) => debounced(e.target.value)} /> */}

        <div>

          <CKEditor
            editor={ClassicEditor}
            data={commentDefaultValue}
            onReady={editor => {
              // You can store the "editor" and use when it is needed.
              console.log('Editor is ready to use!', editor);
            }}
            onChange={(event, editor) => {
              const data = editor.getData();
              debounced(editor.getData())
              // console.log( { event, editor, data } );
            }}
          // onBlur={ ( event, editor ) => {
          //     console.log( 'Blur.', editor );
          // } }
          // onFocus={ ( event, editor ) => {
          //     console.log( 'Focus.', editor );
          // } }
          />
        </div>

        {/* <hr />
        <div dangerouslySetInnerHTML={{ __html: applyClassToTableTag(value) }} className="mt-5"></div> */}
      </Container>
    );

  }
  return null
}

