const balanceIndicators = [

    { id: 1, title: "", type: 'asset' },
    { id: 2, title: "Деньги", type: 'asset' },
    { id: 3, title: "Дебиторская задолженность", type: 'asset' },
    { id: 4, title: "Готовая продукция", type: 'asset' },
    { id: 5, title: "Незавершенное производство", type: 'asset' },
    { id: 6, title: "Материалы", type: 'asset' },

    { id: 7, title: "Основные средства", type: 'asset' },


    { id: 8, title: "", type: "liability" },

    { id: 9, title: "Кредиторская задолженность", type: 'liability' },
    { id: 10, title: "Краткосрочный банковский кредит", type: 'liability' },
    { id: 11, title: "Долгосрочный банковский кредит", type: 'liability' },
    { id: 12, title: "Нераспределенная прибыль", type: 'equity' },

    { id: 13, title: "", type: 'equity' },

    { id: 14, title: "Уставный капитал", type: 'equity' }
]


function processRecords({
    indicator = "Sales",
    balanceIndicators = [],
    records = [],
    periods = ["default"],
    period = "default" }) {
    let DValues = 0;
    let KValues = 0;

    let assets = balanceIndicators.filter(item => item.type === "asset")
        .map(item => item.title);

    Array.isArray(records) && records.forEach(item => {
        let currentOperationPeriodIndex = periods.findIndex(
            (per) => per === item.period
        );

        let periodIndex = periods.findIndex(el => el === period) + 1;
        if (item.d === indicator && periodIndex > currentOperationPeriodIndex) {
            DValues = DValues + parseFloat(item.sum);
        }
        if (item.k === indicator && periodIndex > currentOperationPeriodIndex) {
            KValues = KValues + parseFloat(item.sum);
        }
    });
    if (assets.includes(indicator)) {
        return DValues - KValues;
    } else {
        return KValues - DValues;
    }

};
export default processRecords